/*eslint-disable*/
import React from "react";
import { Link, useLocation } from "react-router-dom";

import NotificationDropdown from "../Dropdowns/NotificationDropdown.js";
import UserDropdown from "../Dropdowns/UserDropdown.js";

export default function Sidebar() {
    const [collapseShow, setCollapseShow] = React.useState("hidden");

    let location = useLocation();
    return (
        <>
            <nav className="md:left-0 md:block md:fixed md:top-0 md:bottom-0 md:overflow-y-auto md:flex-row md:flex-nowrap md:overflow-hidden shadow-xl bg-white flex flex-wrap items-center justify-between relative md:w-64 z-10 py-4 px-6">
                <div className="md:flex-col md:items-stretch md:min-h-full md:flex-nowrap px-0 flex flex-wrap items-center justify-between w-full mx-auto">
                    {/* Toggler */}
                    <button
                        className="cursor-pointer text-black opacity-50 md:hidden px-3 py-1 text-xl leading-none bg-transparent rounded border border-solid border-transparent"
                        type="button"
                        onClick={() =>
                            setCollapseShow("bg-white m-2 py-3 px-6")
                        }
                    >
                        <i className="fas fa-bars"></i>
                    </button>
                    {/* Brand */}
                    <Link
                        to="/"
                        className="text-slate-700 text-xl font-bold leading-relaxed flex items-center mr-4 py-2 whitespace-nowrap capitalize cursor-pointer"
                    >
                        <img
                            className="w-10 h-auto shadow hover:shadow-lg mr-2 scale-125 rounded-full"
                            src={require("../../assets/img/logo/numidia-school-logo.png")}
                        ></img>
                        Numidia
                    </Link>
                    {/* User */}
                    <ul className="md:hidden items-center flex flex-wrap list-none">
                        <li className="inline-block relative">
                            <NotificationDropdown />
                        </li>
                        <li className="inline-block relative">
                            <UserDropdown />
                        </li>
                    </ul>
                    {/* Collapse */}
                    <div
                        className={
                            "md:flex md:flex-col md:items-stretch md:opacity-100 md:relative md:mt-4 md:shadow-none shadow absolute top-0 left-0 right-0 z-40 overflow-y-auto overflow-x-hidden h-auto items-center flex-1 rounded " +
                            collapseShow
                        }
                    >
                        {/* Collapse header */}
                        <div className="md:min-w-full md:hidden block pb-4 mb-4 border-b border-solid border-slate-200">
                            <div className="flex flex-wrap">
                                <div className="w-6/12">
                                    <Link
                                        to="/"
                                        className="text-slate-700 text-xl font-bold leading-relaxed flex items-center mr-4 py-2 whitespace-nowrap capitalize cursor-pointer"
                                    >
                                        <img
                                            className="w-8 h-auto shadow hover:shadow-lg mr-2 scale-125 rounded-full "
                                            src={require("../../assets/img/logo/numidia-school-logo.png")}
                                        ></img>
										Numidia
                                    </Link>
                                </div>
                                <div className="w-6/12 flex justify-end">
                                    <button
                                        type="button"
                                        className="cursor-pointer text-black opacity-50 md:hidden px-3 py-1 text-xl leading-none bg-transparent rounded border border-solid border-transparent"
                                        onClick={() =>
                                            setCollapseShow("hidden")
                                        }
                                    >
                                        <i className="fas fa-times"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                        {/* Form */}
                        <form className="mt-6 mb-4 md:hidden">
                            <div className="mb-3 pt-0">
                                <input
                                    type="text"
                                    placeholder="Search"
                                    className="px-3 py-2 h-12 border border-solid  border-slate-500 placeholder-slate-300 text-slate-600 bg-white rounded text-base leading-snug shadow-none outline-none focus:outline-none w-full font-normal"
                                />
                            </div>
                        </form>

                        {/* Divider */}
                        <hr className="my-4 md:min-w-full" />
                        {/* Heading */}
                        <h6 className="md:min-w-full text-slate-500 text-xs uppercase font-bold block pt-1 pb-4 no-underline">
                            Main pages
                        </h6>
                        {/* Navigation */}

                        <ul className="md:flex-col md:min-w-full flex flex-col list-none">
                            <li className="items-center">
                                <Link
                                    className={
                                        "text-xs uppercase py-3 font-bold block " +
                                        (location.pathname == "/admin/dashboard"
                                            ? "text-lightBlue-500 hover:text-lightBlue-600"
                                            : "text-slate-700 hover:text-slate-500")
                                    }
                                    to="/admin/dashboard"
                                >
                                    <i
                                        className={
                                            "fas fa-tv mr-2 text-sm " +
                                            (location.pathname ==
                                            "/admin/dashboard"
                                                ? "opacity-75"
                                                : "text-slate-300")
                                        }
                                    ></i>{" "}
                                    Dashboard
                                </Link>
                            </li>

                            <li className="items-center">
                                <Link
                                    className={
                                        "text-xs uppercase py-3 font-bold block " +
                                        (location.pathname == "/admin/settings"
                                            ? "text-lightBlue-500 hover:text-lightBlue-600"
                                            : "text-slate-700 hover:text-slate-500")
                                    }
                                    to="/admin/settings"
                                >
                                    <i
                                        className={
                                            "fas fa-tools mr-2 text-sm " +
                                            (location.pathname ==
                                            "/admin/settings"
                                                ? "opacity-75"
                                                : "text-slate-300")
                                        }
                                    ></i>{" "}
                                    Settings
                                </Link>
                            </li>

                            <li className="items-center">
                                <Link
                                    className={
                                        "text-xs uppercase py-3 font-bold block " +
                                        (location.pathname == "/admin/tables"
                                            ? "text-lightBlue-500 hover:text-lightBlue-600"
                                            : "text-slate-700 hover:text-slate-500")
                                    }
                                    to="/admin/tables"
                                >
                                    <i
                                        className={
                                            "fas fa-table mr-2 text-sm " +
                                            (location.pathname ==
                                            "/admin/tables"
                                                ? "opacity-75"
                                                : "text-slate-300")
                                        }
                                    ></i>{" "}
                                    Tables
                                </Link>
                            </li>

                            <li className="items-center">
                                <Link
                                    className={
                                        "text-xs uppercase py-3 font-bold block " +
                                        (location.pathname == "/admin/maps"
                                            ? "text-lightBlue-500 hover:text-lightBlue-600"
                                            : "text-slate-700 hover:text-slate-500")
                                    }
                                    to="/admin/maps"
                                >
                                    <i
                                        className={
                                            "fas fa-map-marked mr-2 text-sm " +
                                            (location.pathname == "/admin/maps"
                                                ? "opacity-75"
                                                : "text-slate-300")
                                        }
                                    ></i>{" "}
                                    Maps
                                </Link>
                            </li>
                        </ul>

                        {/* Divider */}
                        <hr className="my-4 md:min-w-full" />
                    </div>
                </div>
            </nav>
        </>
    );
}
